condiciones donde presta el servicio, es generador de nuevas capacidades productivas a partir del liderazgo de planes de capacitación y entrenamiento. Al igual la gestión de recursos humanos exige la evaluación del desempeño de los trabajadores vista como una oportunidad de mejorar los procesos, identificar necesidades del personal para que puedan desarrollar bien su trabajo. Y no podía faltar el que se encarga de administrar el sistema de compensaciones y beneficios, el cual debe estar acorde a las condiciones promedio del sector.")])]),_c('div',{staticClass:"col-xxl-6 col-xl-6 col-lg-6 col-md-12",attrs:{"data-aos":"fade-down-right"}},[_c('img',{attrs:{"src":require("@/assets/curso/tema10/imagen3.jpg")}})])])])}]

