ento de recursos humanos planea según las necesidades de cada cargo o nivel jerárquico.")])]),_c('div',{staticClass:"col-lg-4 pe-0 pe-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema7/imagen6.jpg")}})])])]),_c('div',{staticClass:"row justify-content-center px-3",attrs:{"titulo":"<span style='margin-left: 1em;'>Evaluación y desempeño</span>"}},[_c('div',{staticClass:"col-lg-8 mb-4 mb-md-0"},[_c('p',[_vm._v("Como soporte logístico de la empresa, uno de los compromisos del área de recursos humanos es lograr el mejoramiento permanente de la organización, haciéndolas más eficientes y más eficaces, donde para ello acude a esquemas de evaluación que definen los pasos a seguir para valorar el rendimiento de cada integrante de la empresa. Tiene como finalidad establecer estrategias para la solución de problemas, orientar la capacitación, identificar conflictos, lograr motivar a los trabajadores y fomentar su desarrollo personal dentro de la empresa. No se debe concebir como una manera de castigar o reprender.")]),_c('p',[_vm._v("Debe ser un proceso permanente y continuo, para que tenga utilidad en la organización. La evaluación del desempeño será realmente útil al trabajador y a la empresa cuando estos sientan que se les ayuda a mejorar en las responsabilidades y funciones, como medida para el crecimiento profesional.")])]),_c('div',{staticClass:"col-lg-4 pe-0 pe-lg-5"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema7/imagen7.jpg")}})])])])])],1)]),_c('div',{staticClass:"tarjeta color-acento-contenido p-3 mt-5 sombra",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"row justify-content-around align-items-center"},[_vm._m(1),_c('div',{staticClass:"col-xxl-10 col-xl-10 col-lg-10 col-md-11 col-sm-12"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(2),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton fondo7",attrs:{"href":_vm.obtenerLink('downloads/EJEMPLO_FORMATO_EVALUACIÓN_DESEMPEÑO.xl